"use strict";
var Prelude = require("../Prelude");
var Data_Maybe = require("../Data.Maybe");
var Control_Monad_Eff = require("../Control.Monad.Eff");
var Control_Monad_Eff_Console = require("../Control.Monad.Eff.Console");
var Control_Monad_Eff_Exception = require("../Control.Monad.Eff.Exception");
var API_PouchDB = require("../API.PouchDB");
var Control_Bind = require("../Control.Bind");
var Control_Applicative = require("../Control.Applicative");
var Data_Unit = require("../Data.Unit");
var callback = function (anyData) {
    return API_PouchDB.logRaw(anyData);
};
var main = (function () {
    var doc = new API_PouchDB.PouchDBDocument({
        id: "hbr123", 
        rev: "2-9AF304BE281790604D1D8A4B0F4C9ADB", 
        name: "Harris", 
        occupation: "Procrastinator", 
        city: "Troisdorf"
    });
    var options = new API_PouchDB.PouchDBOptions({
        name: new Data_Maybe.Just("myDummyDB"), 
        local: new Data_Maybe.Just({
            auto_compaction: true, 
            adapter: Data_Maybe.Nothing.value, 
            revs_limit: 3
        }), 
        remote: new Data_Maybe.Just({
            ajax: new Data_Maybe.Just({
                cache: true, 
                headers: [ "header1", "header2" ], 
                withCredentials: false
            }), 
            auth: new Data_Maybe.Just({
                username: "harris", 
                password: "unbreakable_pwd"
            }), 
            skip_setup: new Data_Maybe.Just(false)
        }), 
        indexdb: Data_Maybe.Nothing.value, 
        websql: Data_Maybe.Nothing.value, 
        sqlite: Data_Maybe.Nothing.value
    });
    return function __do() {
        var v = API_PouchDB.pouchDB(Data_Maybe.Nothing.value)(new Data_Maybe.Just(options))();
        API_PouchDB.info(new Data_Maybe.Just(callback))(v)();
        API_PouchDB.post(doc)(Data_Maybe.Nothing.value)(new Data_Maybe.Just(callback))(v)();
        return Data_Unit.unit;
    };
})();
module.exports = {
    callback: callback, 
    main: main
};



